import { ReactiveVariable } from "vue/macros";
import {
  FormCheckboxProps,
  FormFieldProps,
  FormFieldSelectProps,
} from "@/views/components/form/types";
import { UserRole } from "@/kernel/entities/UserRole";
import { reactive } from "vue";
import { requiredRules } from "@/kernel/validation/vuelidate/requiredRules";
import { phoneRules } from "@/kernel/validation/vuelidate/phoneRules";
import { emailRules } from "@/kernel/validation/vuelidate/emailRules";
import { useStoreUser } from "@/store/user";
import { storeToRefs } from "pinia";
import { maxLengthRules } from "@/kernel/validation/vuelidate/maxLengthRules";

export type UserFormFields = ReactiveVariable<{
  userRole: FormFieldSelectProps<UserRole>;
  name: FormFieldProps;
  phone: FormFieldProps;
  dob: FormFieldProps;
  email: FormFieldProps;
  comment: FormFieldProps;
  is_change_password: FormCheckboxProps;
}>;

export const useFields = () => {
  const storeUser = useStoreUser();
  const { roleOptions } = storeToRefs(storeUser);

  const fields: UserFormFields = reactive({
    userRole: {
      value: null,
      id: "role",
      label: "Тип",
      items: roleOptions,
      rules: requiredRules(),
    },
    name: {
      value: "",
      id: "name",
      label: "ФИО",
      rules: requiredRules(),
    },
    phone: {
      value: "",
      id: "phone",
      label: "Телефон",
      rules: phoneRules(),
    },
    dob: {
      value: "",
      id: "dob",
      label: "Дата рождения",
    },
    email: {
      value: "",
      id: "email",
      label: "E-mail",
      rules: {
        ...emailRules(),
      },
    },
    comment: {
      id: "comment",
      label: "Комментарий",
      value: "",
      rules: maxLengthRules(),
    },
    is_change_password: {
      id: "is_change_password",
      label: "Сбросить пароль",
      value: "is_change_password",
      checked: false,
    },
  });

  return {
    fields,
  };
};
