import { LacSelectOption } from "@/views/components/form/types";
import { UserAddData } from "@/kernel/api/dto/User";
import { UserFormFields } from "@/views/modules/CrudForms/LacUserForm/use/useFields";

export const usePrepareData = (fields: UserFormFields) => {
  function prepareData(): UserAddData {
    const res: UserAddData = {
      name: fields.name.value,
      phone: fields.phone.value,
      role: fields.userRole.value
        ? (fields.userRole.value as LacSelectOption).value
        : "",
    };

    fields.is_change_password.checked &&
      (res.is_change_password = Number(fields.is_change_password.checked));
    fields.email.value && (res.email = fields.email.value);
    fields.dob.value && (res.dob = fields.dob.value);
    fields.comment.value && (res.comment = fields.comment.value);

    return res;
  }

  return { prepareData };
};
