import { Ref, ref } from "vue";
import { useFormValidate } from "@/use/useFormValidate";
import { CrudFormEditInterface } from "@/views/modules/CrudForms/CrudFormInterface";
import { NotImplementedException } from "@/kernel/lib/exceptions/not-implemented.exception";
import {
  useFields,
  UserFormFields,
} from "@/views/modules/CrudForms/LacUserForm/use/useFields";
import { usePrepareData } from "@/views/modules/CrudForms/LacUserForm/use/usePrepareData";
import { UserGetData } from "@/kernel/api/dto/User";
import { useSubmitTemplate } from "@/views/modules/CrudForms/use/useSubmitTemplate";
import { useInitTemplate } from "@/views/modules/CrudForms/use/useInitTemplate";
import { useRouter } from "vue-router";
import { AvailableModalsEnum } from "@/store/modal/types";
import { useStoreModal } from "@/store/modal";
import { useStoreUser } from "@/store/user";
import { storeToRefs } from "pinia";
import {
  getUserList,
  getUserGet,
  postUserEdit,
} from "@/kernel/api/methods/User";

export const useUserEditForm: (
  id: number
) => CrudFormEditInterface<UserFormFields> = (id) => {
  const router = useRouter();
  const modals = useStoreModal();
  const { setFieldRef, validateForm, resetValidations } = useFormValidate();
  const isInit = ref(false);
  const isChanged = ref(true);
  const data: Ref<UserGetData | null> = ref(null);
  const storeUser = useStoreUser();
  const { allUsers } = storeToRefs(storeUser);

  const { fields } = useFields();
  const { prepareData } = usePrepareData(fields);
  const { submitTemplate } = useSubmitTemplate(validateForm);
  const { initTemplate } = useInitTemplate();

  async function fetchData(id: number) {
    const res = await getUserGet(id);
    if (!res.error && res.data.info) {
      data.value = res.data.info;
    }
  }

  async function fillForm() {
    if (data.value) {
      const role = fields.userRole.items.find(
        (role) => role.value === data.value?.role
      );
      role && (fields.userRole.value = role);

      fields.name.value = data.value.name;
      fields.phone.value = data.value.phone;
      data.value.dob && (fields.dob.value = data.value.dob);
      data.value.email && (fields.email.value = data.value.email);
      data.value.comment && (fields.comment.value = data.value?.comment);
    }
  }

  async function init() {
    await fetchData(id);
    await fillForm();
    isInit.value = true;
  }

  async function submit() {
    if (!(await validateForm())) return;
    modals.open(AvailableModalsEnum.LOADER);
    const payload = prepareData();
    const res = await storeUser.edit({ ...payload, user_id: id });

    modals.open(AvailableModalsEnum.SUCCESS, {
      text: res.data.password
        ? `Пароль пользователя: ${res.data.password}`
        : undefined,
      callback: () => router.replace({ name: "adminUsers" }),
    });
  }

  return {
    setFieldRef,
    validateForm,
    resetValidations,
    fields,
    init: initTemplate(init),
    submit: submit,
    isInit,
    isChanged,
    fetchData,
    fillForm,
  };
};
